import axios        from '../util/axios';
import { baseUrl }  from './config';

// 获取名片的详情
export function getCardInfo(data) {
    let url = baseUrl + '/Api/BusinessCard/getCarInfo';
    return axios.post(url, {...data})
        .then(res => { return Promise.resolve(res) });
}


