<template>
    <div class="me" v-if="info">
        <div class="main">
            <img class="avatar" :src="info.head_img" alt="">
            <cell :title="'公司名称：' + info.company" />
            <cell :title="'姓名：' + info.name" />
            <cell :title="'职位：' + info.position" />
            <cell :title="'电话：' + info.phone" />
            <cell :title="'邮件：' + info.email" />
        </div>
        <div class="bt-line">
            <cell title="我的名片" is-link v-if="info.code_img" @click="goCodeImg" />
            <cell title="个性签名" :value="info.signature" />
            <div class="van-cell van-hairline me" v-if="info.describe_imgs.length > 0">
                <div class="van-cell__title"><span>上传图片</span></div>
                <div class="van-cell__value">
                    <img v-for="(url, index) of info.describe_imgs" :key="index"
                        :src="url" alt="">
                </div>
            </div>
            <cell title="使用介绍" is-link />
            <cell title="意见反馈" is-link />
        </div>
    </div>
</template>

<script>
import { Cell, CellGroup }  from 'vant';
import { getCardInfo }      from '../../server/me';
import { getItem }          from '../../util/util';
export default {
    data () {
       return {
           cardId   : 0,
           info     : null
       };
    },
    created() {
        if(getItem('cardId')) {
            this.cardId = getItem('cardId');
        };
        this._getCardInfo();
    },
    methods: {
        async _getCardInfo() {
            let res = await getCardInfo({id1: this.cardId, user_id: ''});
            if(res.data.status === 1) {
                this.info = res.data.data;
            }
        },
        goCodeImg() {
            this.$router.push(`/codeimg?url=${encodeURIComponent(this.info.code_img)}`)
        }
    },
    components: {
        Cell,
        CellGroup
    }
}
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>


